@import "_variables";
@import "_grids";
@import "_fonts";
@import "_typo";
@import "_breadcrumbs";
@import "_contact-form";

* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  line-height: $spacing;
  font-weight: 100;
  min-width: 320px;
  max-width: 1920px;
}
@import "_info-panel";

.main-panel {
  padding: $spacing $spacing/2;
}

@media (min-width: $lg-xl-width) {

  .main-panel {
    margin-left: 480px;
    padding: 2*$spacing;
  }

}

h1 {
  margin: 0 0 45px;
  font-weight: 300;
  font-size: 30px;
  line-height: 45px;
}

h2 {
  margin: 0 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 60px;
}

p, ol, ul {
  margin: 0 0 $spacing;
  font-weight: 100;
}

strong {
  font-weight: bold;
}