.info-panel {
  &, a {
    color: white;
  }
  background-color: #007fbf;
  background-image: url('/assets/images/marble-480.png');
  background-size: cover;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.3);
  min-width: 320px;
  padding: $spacing;
  overflow-y: auto;
}

.identity {
  margin: 0;
  .photo {
    float: left;
    height: 2*$spacing;
    width: auto;
  }
  .name, .slogan {
    line-height: $spacing;
    font-weight: 100;
    margin: 0;
  }
  .name {
    font-size: 24px;
  }
  .slogan {
    font-size: 16px;
  }
}

.contacts {
  margin: $spacing 0 0;
  div {
    font-size: 14px;
    line-height: 30px;
    padding: $spacing 0;
    i {
      font-size: 20px;
      width: 100%;
      line-height: $spacing;
    }
    span {
      line-height: $spacing;
    }
  }
  .mail, .web {
    border-bottom: 1px dotted #fff;    
  }
}

@media (min-width: $sm-md-width) {

  .info-panel {
    padding: 2*$spacing;
  }
  .identity {
    .photo {
      width: 3*$spacing;
      height: 3*$spacing;
    }
    .name, .slogan {
      line-height: 45px;
    }
    .name {
      font-size: 30px;
    }
    .slogan {
      font-size: 20px;
    }
  }
  .contacts {
    div {
      font-size: 18px;
      i {
        font-size: 30px;
        line-height: 60px;
      }
    }
  }
  
}

@media (min-width: $md-lg-width) {

  .identity {
    width: 360px;
    margin: 0 auto;
  }
  .contacts {
    .mail, .web {
      border-bottom: none;    
    }
  }

}

@media (min-width: $lg-xl-width) {

  .info-panel {
    width: 480px;
    top: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    box-shadow: 5px 0 5px 0 rgba(0, 0, 0, 0.3);
  }

  .contacts {
    .mail, .web {
      border-bottom: 1px dotted #fff;    
    }
  }

}