$xs-sm-width: 320px;
$sm-md-width: 480px;
$md-lg-width: 768px;
$lg-xl-width: 992px;

.xs-6 {
  float: left;
  width: 50%;
}

.sm-6 {
  @media (min-width: $xs-sm-width) {
    float: left;
    width: 50%;
  }
}

.md-6 {
  @media (min-width: $sm-md-width) {
    float: left;
    width: 50%;
  }
}

.lg-3 {
  @media (min-width: $md-lg-width) {
    float: left;
    width: 25%;
  }
}

.xl-6 {
  @media (min-width: $lg-xl-width) {
    float: left;
    width: 50%;
  } 
}